@import 'src/scss/settings/mixins';
@import 'src/scss/settings/font';

.carouselContainer {
  width: 100%;
  overflow: hidden;
  h1 {
    width: 50%;
    position: absolute;
    top: -60rem;
    left: -20rem;
    font-size: 8rem;
    color: black;
    text-align: start;
  }
  h2 {
    font-size: 10rem;
    color: white;
    position: absolute;
    top: -30rem;
    left: 0rem;
  }
  h3 {
    font-size: 10rem;
    color: black;
  }
  h5 {
    font-size: 12rem;
    color: black;
    margin-left: 6  5rem;
  }
  p {
    font-size: 5rem;
    color: black;
    font-family: font-second;
    text-align: start;
  }
  .weddingText {
    font-size: 4rem;
    color: white;
    font-family: font-second;
    position: absolute;
    top: -15rem;
    left: -2rem;
  }
  .qaText {
    position: absolute;
    top: -47rem;
    left: 40rem;
  }
  .hoverText {
    margin-bottom: 8%;
    font-size: 4rem;
    color: black;
    opacity: 0;
    transition: 1s;
    h4 {
      margin-top: 8%;
      font-size: 7rem;
      color: black;
      font-family: font-second;
    }
  }

  img {
    height: 80rem;
    object-position: 0 20%;
    object-fit: cover;
    transition: 1s;
  }
  .hoverClass:hover {
    img {
      background-color: white;
      opacity: 0.3;
    }
    h3 {
      opacity: 0;
      transition: 1s;
    }
    h4 {
      opacity: 0;
      transition: 1s;
    }
    .hoverText {
      opacity: 1;
    }
  }
}

@include tabletMax {
  .carouselContainer {
    h1 {
      font-size: 9rem;
      margin: 20% auto;
    }
    h2 {
      font-size: 9rem;
    }
  }
}

@include mobile {
  .carouselContainer {
    margin-top: 13rem;

    h1 {
      width: 100%;
      margin: 0 auto;
      margin-bottom: 50%;
      font-size: 5rem;
    }
    h2 {
      font-size: 5rem;
    }
    .hoverText {
      h3 {
        margin-top: 8%;
        font-size: 3.5rem;
      }
    }
    h4 {
      font-size: 3.5rem;
    }
    p {
      font-size: 2.5rem;
    }
    img {
      min-height: 58rem;
      max-height: 68rem;
      margin-top: 0;
    }
    .hoverClass:hover {
      img {
        opacity: 1;
      }
      .hoverText {
        display: none;
      }
    }
  }
}
